import Vue from 'vue'
import Router from 'vue-router'
import good from '@/components/goods/goods.vue'
import rating from '@/components/ratings/ratings.vue'
import seller from '@/components/seller/seller.vue'
import VueResource from 'vue-resource'
import '../common/stylus/index.styl'

Vue.use(Router)
Vue.use(VueResource)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: {
        name: 'good'
      }
    },
    {
      path: '/goods',
      name: 'good',
      component: good
    },
    {
      path: '/ratings',
      name: 'rating',
      component: rating
    },
    {
      path: '/seller',
      name: 'seller',
      component: seller
    }
  ]
})



