<template>
  <div class="header">
    <div class="content-wrapper">
  	  <div class="avatar">
  		  <img width="64" height="64" :src="seller.avatar">
  	  </div>
  	  <div class="content">
  		  <div class="title">
  			  <span class="brand"></span>
  			  <span class="name">{{seller.name}}</span>
  		  </div>
  		  <div class="description">
  			  {{seller.description}}/{{seller.deliveryTime}}分钟送达
  		  </div>
  		  <div v-if="seller.supports" class="support" >
  			  <span class="icon" :class="classMap[seller.supports[0].type]"></span>
  			  <span class="text">{{seller.supports[0].description}}</span>
  		  </div>
  	  </div>
  	  <div v-if="seller.supports" class="support-count" @click="showDetail">
  	  	<span class="count">{{seller.supports.length}}个</span>
  	  	<i class="icon-keyboard_arrow_right"></i>
  	  </div>
    </div>
    <div class="bulletin-wrapper" @click="showDetail">
        <span class="bulletin-title"></span>
    	<span class="bulletin-text">{{seller.bulletin}}</span>
    	<span class="icon-keyboard_arrow_right"></span>
    </div>
    <div class="background">
    	<img :src="seller.avatar" width="100%" height="100%">
    </div>
    <transition name="fade">
    <div class="detail" v-show="detailShow">
    	<div class="detail-wrapper clearfix">
    		<div class="detail-main">
        <h1 class="name">{{seller.name}}</h1>
        <div class="star-wrapper">
          <star :size="48" :score="seller.score"></star>
        </div>
        <div class="detail-title">
          <div class="line"></div>
          <div class="text">优惠信息</div>
          <div class="line"></div>
        </div>
        <ul v-if="seller.supports" class="supports">
          <li v-for="(item,index) in seller.supports" class="support-item">
            <span class="icon" :class="classMap[index]"></span>
            <span class="text">{{seller.supports[index].description}}</span>
          </li>
        </ul>
        <div class="detail-title">
          <div class="line"></div>
          <div class="text">商家公告</div>
          <div class="line"></div>
        </div>
          <div class="bulletin">
            <p class="content">{{seller.bulletin}}</p>
          </div>
    		</div>
    	</div>
    		<div class="detail-close" @click="closeDetail">
    			<i class="icon-close"></i>
    		</div>
    </div>
    </transition>
   </div>
</template>

<script type="text/ecmascript-6">
import star from '../../components/star/star'

export default{
  props: {
    seller: {
      type: Object
    }
  },
  data() {
    return {
      detailShow: false
    }
  },
  methods: {
    showDetail() {
      this.detailShow = true
    },
    closeDetail() {
      this.detailShow = false
    }
  },
  created() {
    this.classMap = ['decrease', 'discount', 'guarantee', 'invoice', 'special']
  },
  components: {
    star
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
 @import "../../common/stylus/mixin.styl"

.header 
	color: #fff
	overflow: hidden
	position: relative
	background-color: rgba(7, 17, 27, 0.5)
	.content-wrapper
		padding: 24px 12px 18px 24px
		position: relative
		font-size: 0
		.avatar
			display: inline-block
			vertical-align: top
			img
				border-radius: 2px
		.content
			display: inline-block
			font-size: 14px
			margin-left: 16px
			.title
				margin: 2px 0 8px 0
				.brand
					width: 30px
					height:18px
					display: inline-block
					vertical-align: top
					bg-image("brand")
					background-size: 30px 18px
					backgrpund-repeat: no-repeat
  .name
					margin-left: 6px
					font-size: 16px
					line-height: 18px
					font-weight: bold
			.description
				margin-bottom: 10px
				line-height: 12px
				font-size: 12px
		.support
				.icon
					display: inline-block
					width: 12px
					height: 12px
					margin-right: 4px
					background-size: 12px 12px
					background-repeat: no-repeat
					&.decrease
						bg-image("decrease_1")
					&.discount
						bg-image("discount_1")
					&.guarantee
						bg-image("guarantee_1")
					&.invoice
						 bg-image("invoice_1")
					&.special
						bg-image("special_1")
				.text
					line-height: 12px
					vertical-align: top
					font-size: 10px          
		.support-count
			position: absolute
			right: 12px
			bottom: 14px
			padding: 0 8px
			height: 24px
			line-height: 24px
			border-radius: 14px
			background-color: rgba(0,0,0,0.2)
			text-align: center
			.count
				font-size: 10px
				vertical-align: top
			.icon-keyboard_arrow_right
				line-height: 24px
				font-size: 10px
				margin-left 2px
	.bulletin-wrapper
		background-color: rgba(7, 17, 27, 0.2)
		position: relative
		height: 28px
		line-height: 28px
		padding: 0 22px 0 12px
		white-space: nowrap
		overflow: hidden
		text-overflow: ellipsis
		.bulletin-title
			display: inline-block
			width: 22px
			height: 12px
			bg-image("bulletin")
			background-size: 22px 12px
			background-repeat: no-repeat
		.bulletin-text
			margin:0 4px
			font-size: 10px
			vertical-align: top
		.icon-keyboard_arrow_right
			position: absolute
			right: 12px
			top: 6px
			vertical-align: top
	.background
		position: absolute
		left: 0px
		top: 0px
		width: 100%
		height: 100%
		z-index: -1
		filter: blur(10px)
	.detail
		position: fixed
		width: 100%
		height: 100%
		top: 0
		left: 0
		overflow: auto
		background-color: rgba(7, 17, 27, 0.8)
		z-index: 20
  &.fade-enter-active,&.fade-leave-active
    transition: all 0.5s
  &.fade-enter,&.fade-leave-active
    opacity: 0  
	  .detail-wrapper
			min-height: 100%
   width 100%
   overflow: auto
			.detail-main
			  margin-top: 64px
				padding-bottom: 64px
.name
          text-align: center
          line-height: 16px
          height: 16px
 .star-wrapper
          margin-top: 18px
          padding: 2px 0
          text-align: center
       .detail-title
          display: flex
          width: 80%
          margin: 28px auto 24px auto
       .text
         font-size: 14px
         padding 0 12px
         font-weight 28px
       .line
         border-bottom: 1px solid rgba(255,255,255,0.2)
         position: relative
         flex: 1
         top: -6px
       .supports
         width: 80%
         margin: 0 auto
         .support-item
           padding: 0 12px
           margin-bottom: 12px
           font-size: 0
         &:last-child
           margin-bottom: 0
         .icon
           display: inline-block
           width: 16px
           height: 16px
           vertical-align: top
           margin-right: 6px
           background-size: 16px 16px
           background-repeat: no-repeat
           &.decrease
             bg-image("decrease_1")
           &.discount
             bg-image("discount_1")
           &.guarantee
             bg-image("guarantee_1")
            &.invoice
             bg-image("invoice_1")
           &.special
             bg-image("special_1")
         .text-align
           line-height: 16px
           font-size: 12px
       .bulletin
         margin: 0 auto
         width: 80%
         .content
           padding: 0 12px
           font-size: 12px
           line-height: 24px
		 .detail-close
			position: relative
			width: 32px
			height: 32px
			margin: -64px auto 0 auto
			clear: both
			font-size: 32px
			clear: both
</style>



